import React, { Component } from "react";
import "./App.css";
import Form from "./components/Form";
import Recipes from "./components/Recipes";
import { Link } from "react-router-dom";
import LoadingScreen from "./components/svg/landingLoading";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";

const API_KEY = "08ee1b4613c342c86f1bf5d398502cff";

//  "2ba01c1d23194ea90f8e8addd404c6b2";

class App extends Component {
  state = {
    recipes: [],
    count: 5
  };

  getRecipe = async e => {
    const recipeName = e.target.elements.recipeName.value;
    e.preventDefault();
    const api_call = await fetch(
      `https://cors-anywhere.herokuapp.com/http://food2fork.com/api/search?key=${API_KEY}&q=${recipeName}&count=${
        this.state.count
      }`
    );
    const data = await api_call.json();
    console.log(data);
    this.setState({
      recipes: data.recipes
    });
  };

  componentDidMount = () => {
    const json = localStorage.getItem("recipes");
    const recipes = JSON.parse(json);
    this.setState({
      recipes
    });
  };

  componentDidUpdate = () => {
    const recipes = JSON.stringify(this.state.recipes);
    localStorage.setItem("recipes", recipes);
  };

  handleChange = e => {
    this.setState({
      count: e.target.value
    });
  };

  scrollToRecipes = () => {
    scroll.scrollTo();
  };

  scrollToTop = () => {
    scroll.scrollToTop();
  };

  render() {
    return (
      <div className="App">
        {
          <LinkScroll
            activeClass="active"
            to="container"
            spy={true}
            smooth={true}
            offset={-70}
            duration={1100}
            onSetActive={this.scrollToRecipes}
          >
            {<LoadingScreen id="landing" />}
            <div align="center">
              <a
                className="buttonExplore"
                href="add-website-here"
                target=""
                rel="nofollow noopener"
                style={{ textDecoration: "none" }}
              >
                Click here to explore
              </a>
            </div>
          </LinkScroll>
        }
        <LinkScroll
          activeClass="active"
          to="landing"
          spy={true}
          smooth={true}
          offset={-70}
          duration={1100}
          onSetActive={this.scrollToTop}
        >
          <header className="App-header">
            <h1 className="App-title">
              <a
                href="add-website-here"
                target="landing"
                rel="nofollow noopener"
                style={{ textDecoration: "none" }}
              >
                Recipe Search
              </a>
            </h1>{" "}
          </header>{" "}
        </LinkScroll>
        <Form getRecipe={this.getRecipe} />{" "}
        <div
          className="radioButtons"
          style={{
            marginBottom: "1rem"
          }}
        >
          <input
            type="radio"
            value="5"
            defaultChecked
            name="count"
            onChange={this.handleChange}
          />
          5{" "}
          <input
            type="radio"
            value="10"
            name="count"
            onChange={this.handleChange}
          />
          10{" "}
          <input
            type="radio"
            value="20"
            name="count"
            onChange={this.handleChange}
          />
          20{" "}
          <input
            type="radio"
            value="30"
            name="count"
            onChange={this.handleChange}
          />
          30{" "}
        </div>{" "}
        {
          <Recipes
            id="recipes"
            recipes={this.state.recipes}
            count={this.state.count}
          />
        }{" "}
      </div>
    );
  }
}
//localStorage.getItem("recipes")
export default App;
